import axios from "axios";

const token = localStorage.getItem("token");

export default {
  async createUser(dati) {
    return axios.post("/WPExamProject/api/user/create", JSON.stringify(dati), {
      headers: {
        "Content-type": "application/json",
      },
    });
  },

  async authenticateUser(dati) {
    return axios.post(
      "/WPExamProject/api/user/authenticate",
      JSON.stringify(dati),
      {
        headers: {
          "Content-type": "application/json",
        },
      }
    );
  },

  async updateUser(dati) {
    return axios.put("/WPExamProject/api/user/update", JSON.stringify(dati), {
      headers: {
        "Content-type": "application/json",
        "x-access-token": token,
      },
    });
  },
};
